import { assign, createMachine } from "xstate";
import { ThemeManager } from "../themes/ThemeManagerModule";
import { UserPreferenceModule } from "../prefs/PreferenceModule";
import { AudibleNotificationsModule } from "../NotificationsModule";

export function createThemeToggleMachine(themeManager: ThemeManager) {
  const audible = AudibleNotificationsModule.getInstance();
  const userPreferences = UserPreferenceModule.getInstance();

  const machine = createMachine(
    {
      /** @xstate-layout -Clif72Er5FjIpEKexbFjbOnZPZbZSuaTSexqB2ndzK2rE7l3R7PbURb7k4UINRWxZ6FgsewSErqGQuWEY-KFZxW5Qu3JKbFuIA */
      context: {
        theme: "light",
      },
      id: "themeToggle",
      initial: "loading",
      states: {
        loading: {
          description: "Determining the initial theme mode.",
          invoke: {
            id: "getPreferedTheme",
            src: () => userPreferences.getTheme(),
            onDone: {
              target: "loaded",
              actions: assign({ theme: (context, event) => event.data }),
            },
            onError: {
              target: "Normal mode",
            },
          },
        },
        loaded: {
          description: "The initial theme mode has been determined.",
          always: [
            {
              target: "Normal mode",
              cond: "preferLight",
            },
            {
              target: "Dark mode",
              cond: "preferDark",
            },
          ],
        },
        "Normal mode": {
          description: "The UI is in normal (default) mode with a light theme.",
          entry: "enterNormalMode",
          on: {
            toggle: {
              target: "Dark mode",
              actions: [
                {
                  type: "saveMode",
                  params: {
                    theme: "dark",
                  },
                },
                {
                  type: "soundEffectOn",
                },
              ],
            },
          },
        },
        "Dark mode": {
          description: "The UI is in dark mode with a dark theme.",
          entry: "enterDarkMode",
          on: {
            toggle: {
              target: "Normal mode",
              actions: [
                {
                  type: "saveMode",
                  params: {
                    theme: "light",
                  },
                },
                {
                  type: "soundEffectOff",
                },
              ],
            },
          },
        },
      },
      schema: {
        events: {} as { type: "toggle" },
        context: {} as { theme: string },
      },
      predictableActionArguments: true,
      preserveActionOrder: true,
    },
    {
      actions: {
        enterDarkMode: (context, event) => {
          themeManager.applyDarkMode();
        },
        enterNormalMode: (context, event) => {
          themeManager.applyNormalMode();
        },
        saveMode: (context, event, { action }) => {
          userPreferences.setTheme(action.params.theme);
        },
        soundEffectOn: () => {
          audible.themeOn();
        },
        soundEffectOff: () => {
          audible.themeOff();
        },
      },
      services: {},
      guards: {
        preferLight: (context) => context.theme === "light",
        preferDark: (context) => context.theme === "dark",
      },
      delays: {},
    }
  );
  return machine;
}
